body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to right, #d9d9d9, #f6f2f2);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0px;
  padding: 0px;
}

.App {
  text-align: center;
}

.centerContent {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.part-bg {
  position: fixed;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  z-index: -1;
}
.NavBtn {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}
.Tilt {
    background: linear-gradient(to right, #d9d9d9, #f6f2f2);
}
.bounding-box {
    position: absolute;
    box-shadow: inset 0 0 0 3px #fffb00;
    /* display: -ms-flexbox; */
    display: -webkit-flex;
    display: flex;
    /* -ms-flex-wrap: wrap; */
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    /* -ms-flex-pack: center; */
    -webkit-justify-content: center;
            justify-content: center;
    cursor: pointer;
}
